@import url(https://fontlibrary.org/face/robert-sans);
*{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;margin:0;padding:0}html{font-size:calc(16/6.32*1vmin)}.CurrentWeather{width:78vw;height:90vh;left:0;top:0;display:flex;flex-direction:row;justify-content:space-between;margin:5vh 12vw 5vh 10vw}.CurrentWeather .column1{display:flex;flex-direction:column;justify-content:space-evenly}.CurrentWeather .column1 .temperature{font-size:15em}.CurrentWeather .column1 .city{font-size:7em;font-style:italic}.CurrentWeather .column2{right:0;display:flex;flex-direction:column;justify-content:space-evenly;text-align:end;align-items:flex-end;font-family:RobertSansExtraLight, sans-serif !important}.CurrentWeather .column2 .summary{font-size:4em;width:-webkit-min-content;width:-moz-min-content;width:min-content}.WeatherBox{display:flex;overflow-x:scroll}.WeatherBox .Card{flex:1 1;display:flex;flex-direction:column;align-items:stretch;padding:1em;text-align:center;min-width:120px;cursor:pointer}.WeatherBox .Card .Date{font-family:RobertoSansBold;font-size:larger}.WeatherBox .Card .Icon{font-size:5em;margin-bottom:0;align-self:center}.WeatherBox .Card .Temperature{display:flex;justify-content:space-evenly}.WeatherBox .Card .Temperature .Low{font-family:RobertoSansLight}.WeatherBox .Card .Summary{font-size:0.75em;color:grey}.WeatherBox .Card:hover{background-color:lightgray}

